'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import {
    Shield,
    Bell,
    User,
    Globe,
    AlertTriangle,
    Activity,
    MapPin,
    Clock,
    Zap,
    TrendingUp
} from 'lucide-react';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { SettingsModal } from '@/components/settings-modal';
import { getThreatMapData, getLiveThreats, getThreatRegions } from '@/lib/api';
import type { ThreatData, LiveThreat, SeverityLevel } from '@/types';

export default function ThreatMapPage() {
    const [threats, setThreats] = useState<ThreatData[]>([]);
    const [liveThreats, setLiveThreats] = useState<LiveThreat[]>([]);
    const [regions, setRegions] = useState<Array<{ region: string; count: number }>>([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        loadData();
        // Refresh live threats every 30 seconds
        const interval = setInterval(loadLiveThreats, 30000);
        return () => clearInterval(interval);
    }, []);

    const loadData = async () => {
        setIsLoading(true);
        try {
            const [threatData, live, regionData] = await Promise.all([
                getThreatMapData(),
                getLiveThreats(),
                getThreatRegions()
            ]);
            setThreats(threatData);
            setLiveThreats(live);
            setRegions(regionData);
        } catch (error) {
            console.error('Failed to load threat data:', error);
        } finally {
            setIsLoading(false);
        }
    };

    const loadLiveThreats = async () => {
        try {
            const live = await getLiveThreats();
            setLiveThreats(live);
        } catch (error) {
            console.error('Failed to refresh live threats:', error);
        }
    };

    const getSeverityColor = (severity: SeverityLevel) => {
        switch (severity) {
            case 'critical': return 'bg-red-500';
            case 'suspicious': return 'bg-yellow-500';
            case 'safe': return 'bg-green-500';
        }
    };

    const getSeverityBadge = (severity: SeverityLevel) => {
        const styles = {
            critical: 'bg-red-100 text-red-800 dark:bg-red-900/30 dark:text-red-400',
            suspicious: 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/30 dark:text-yellow-400',
            safe: 'bg-green-100 text-green-800 dark:bg-green-900/30 dark:text-green-400',
        };
        return <Badge className={styles[severity]}>{severity.toUpperCase()}</Badge>;
    };

    const formatTime = (timestamp: string) => {
        const date = new Date(timestamp);
        const now = new Date();
        const diff = Math.floor((now.getTime() - date.getTime()) / 1000);
        if (diff < 60) return `${diff}s ago`;
        if (diff < 3600) return `${Math.floor(diff / 60)}m ago`;
        return `${Math.floor(diff / 3600)}h ago`;
    };

    const stats = {
        total: threats.length,
        critical: threats.filter(t => t.severity === 'critical').length,
        suspicious: threats.filter(t => t.severity === 'suspicious').length,
    };

    return (
        <div className="min-h-screen bg-gradient-to-b from-slate-50 to-slate-100 dark:from-slate-950 dark:to-slate-900">
            {/* Navigation */}
            <nav className="sticky top-0 z-50 bg-white/80 dark:bg-slate-900/80 backdrop-blur-md border-b border-slate-200 dark:border-slate-800">
                <div className="container mx-auto px-4">
                    <div className="flex h-16 items-center justify-between">
                        <div className="flex items-center gap-8">
                            <Link href="/" className="flex items-center gap-2">
                                <div className="h-9 w-9 rounded-lg bg-gradient-to-br from-indigo-500 to-purple-600 flex items-center justify-center">
                                    <Shield className="h-5 w-5 text-white" />
                                </div>
                                <span className="font-bold text-lg">PhishGuard</span>
                            </Link>
                            <div className="hidden md:flex items-center gap-6">
                                <Link href="/dashboard" className="text-sm font-medium text-slate-500 hover:text-slate-900 dark:hover:text-white">Dashboard</Link>
                                <Link href="/history" className="text-sm font-medium text-slate-500 hover:text-slate-900 dark:hover:text-white">History</Link>
                                <Link href="/threat-map" className="text-sm font-medium text-slate-900 dark:text-white">Threat Map</Link>
                                <Link href="/settings" className="text-sm font-medium text-slate-500 hover:text-slate-900 dark:hover:text-white">Settings</Link>
                            </div>
                        </div>
                        <div className="flex items-center gap-3">
                            <Button variant="ghost" size="icon">
                                <Bell className="h-5 w-5" />
                            </Button>
                            <SettingsModal />
                            <div className="h-9 w-9 rounded-full bg-gradient-to-br from-amber-400 to-orange-500 flex items-center justify-center">
                                <User className="h-5 w-5 text-white" />
                            </div>
                        </div>
                    </div>
                </div>
            </nav>

            {/* Header */}
            <div className="border-b border-slate-200 dark:border-slate-800 bg-white/50 dark:bg-slate-900/50">
                <div className="container mx-auto px-4 py-8">
                    <div className="flex items-center gap-3 mb-2">
                        <Globe className="h-8 w-8 text-indigo-600" />
                        <h1 className="text-3xl font-bold text-slate-900 dark:text-white">Global Threat Map</h1>
                    </div>
                    <p className="text-slate-600 dark:text-slate-400">Real-time visualization of phishing attacks worldwide</p>
                </div>
            </div>

            <div className="container mx-auto px-4 py-6">
                <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                    {/* Main Map Area */}
                    <div className="lg:col-span-2 space-y-6">
                        {/* Stats */}
                        <div className="grid grid-cols-3 gap-4">
                            <div className="bg-white dark:bg-slate-800 rounded-xl p-4 border border-slate-200 dark:border-slate-700">
                                <div className="flex items-center gap-2 mb-2">
                                    <Activity className="h-5 w-5 text-indigo-600" />
                                    <span className="text-sm text-slate-500">Active Threats</span>
                                </div>
                                <p className="text-2xl font-bold text-slate-900 dark:text-white">{stats.total}</p>
                            </div>
                            <div className="bg-white dark:bg-slate-800 rounded-xl p-4 border border-slate-200 dark:border-slate-700">
                                <div className="flex items-center gap-2 mb-2">
                                    <AlertTriangle className="h-5 w-5 text-red-600" />
                                    <span className="text-sm text-slate-500">Critical</span>
                                </div>
                                <p className="text-2xl font-bold text-red-600">{stats.critical}</p>
                            </div>
                            <div className="bg-white dark:bg-slate-800 rounded-xl p-4 border border-slate-200 dark:border-slate-700">
                                <div className="flex items-center gap-2 mb-2">
                                    <Zap className="h-5 w-5 text-yellow-600" />
                                    <span className="text-sm text-slate-500">Suspicious</span>
                                </div>
                                <p className="text-2xl font-bold text-yellow-600">{stats.suspicious}</p>
                            </div>
                        </div>

                        {/* Map Placeholder */}
                        <div className="bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700 overflow-hidden">
                            <div className="aspect-video bg-gradient-to-br from-slate-100 to-slate-200 dark:from-slate-700 dark:to-slate-800 relative flex items-center justify-center">
                                <div className="text-center">
                                    <Globe className="h-16 w-16 text-slate-400 mx-auto mb-4" />
                                    <p className="text-slate-500 dark:text-slate-400">Interactive Map</p>
                                    <p className="text-sm text-slate-400 dark:text-slate-500 mt-2">
                                        Integration with Leaflet or Mapbox coming soon
                                    </p>
                                </div>

                                {/* Animated threat dots */}
                                {threats.slice(0, 10).map((threat, i) => (
                                    <div
                                        key={threat.threat_id}
                                        className={`absolute w-3 h-3 rounded-full ${getSeverityColor(threat.severity)} animate-ping`}
                                        style={{
                                            left: `${20 + (i * 7)}%`,
                                            top: `${30 + (i % 3) * 20}%`,
                                            animationDelay: `${i * 0.2}s`,
                                            animationDuration: '2s'
                                        }}
                                    />
                                ))}
                            </div>
                        </div>

                        {/* Top Source Regions */}
                        <div className="bg-white dark:bg-slate-800 rounded-xl p-5 border border-slate-200 dark:border-slate-700">
                            <div className="flex items-center gap-2 mb-4">
                                <TrendingUp className="h-5 w-5 text-indigo-600" />
                                <h3 className="font-semibold text-slate-900 dark:text-white">Top Source Regions</h3>
                            </div>
                            <div className="space-y-3">
                                {regions.map((region, i) => (
                                    <div key={region.region} className="flex items-center gap-3">
                                        <span className="text-sm text-slate-500 w-6">{i + 1}.</span>
                                        <div className="flex-1">
                                            <div className="flex items-center justify-between mb-1">
                                                <span className="text-sm font-medium text-slate-900 dark:text-white">{region.region}</span>
                                                <span className="text-sm text-slate-500">{region.count.toLocaleString()}</span>
                                            </div>
                                            <div className="h-2 bg-slate-100 dark:bg-slate-700 rounded-full overflow-hidden">
                                                <div
                                                    className="h-full bg-gradient-to-r from-indigo-500 to-purple-500 rounded-full"
                                                    style={{ width: `${(region.count / regions[0].count) * 100}%` }}
                                                />
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>
                    </div>

                    {/* Live Feed Sidebar */}
                    <div className="space-y-6">
                        <div className="bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700 overflow-hidden">
                            <div className="px-5 py-4 border-b border-slate-200 dark:border-slate-700 flex items-center justify-between">
                                <div className="flex items-center gap-2">
                                    <div className="h-2 w-2 rounded-full bg-red-500 animate-pulse" />
                                    <h3 className="font-semibold text-slate-900 dark:text-white">Live Threat Feed</h3>
                                </div>
                                <Button variant="ghost" size="sm" onClick={loadLiveThreats}>
                                    Refresh
                                </Button>
                            </div>
                            <div className="max-h-[600px] overflow-y-auto">
                                {liveThreats.map((threat) => (
                                    <div
                                        key={threat.id}
                                        className="px-5 py-4 border-b border-slate-100 dark:border-slate-700/50 hover:bg-slate-50 dark:hover:bg-slate-700/20"
                                    >
                                        <div className="flex items-start justify-between gap-3 mb-2">
                                            <span className="text-sm font-medium text-slate-900 dark:text-white">
                                                {threat.label}
                                            </span>
                                            {getSeverityBadge(threat.severity)}
                                        </div>
                                        <p className="text-sm text-slate-500 mb-2 truncate">{threat.entity}</p>
                                        <div className="flex items-center justify-between text-xs text-slate-400">
                                            <div className="flex items-center gap-1">
                                                <MapPin className="h-3 w-3" />
                                                {threat.location}
                                            </div>
                                            <div className="flex items-center gap-1">
                                                <Clock className="h-3 w-3" />
                                                {formatTime(threat.timestamp)}
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>

                        {/* Recent Threats */}
                        <div className="bg-white dark:bg-slate-800 rounded-xl p-5 border border-slate-200 dark:border-slate-700">
                            <h3 className="font-semibold text-slate-900 dark:text-white mb-4">Attack Types</h3>
                            <div className="space-y-3">
                                <div className="flex items-center justify-between">
                                    <span className="text-sm text-slate-600 dark:text-slate-400">Credential Harvesting</span>
                                    <Badge variant="secondary">45%</Badge>
                                </div>
                                <div className="flex items-center justify-between">
                                    <span className="text-sm text-slate-600 dark:text-slate-400">Malware Distribution</span>
                                    <Badge variant="secondary">32%</Badge>
                                </div>
                                <div className="flex items-center justify-between">
                                    <span className="text-sm text-slate-600 dark:text-slate-400">Social Engineering</span>
                                    <Badge variant="secondary">23%</Badge>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
